html {
    font-size: 62.5%;
    box-sizing: border-box;
    scroll-padding-top: 0rem;

}
*, 
*:before,
*:after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
.contenedor {
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}
img {
    max-width: 100%;
    width: 100%;
    height: auto;
    display: block;
}